img {
    margin-bottom: 10px;
}

ul {
    color: black;
}
.rounded{
    border: 8px solid #dfdfdf;
    border-radius: 400px!important;
    -webkit-border-radius: 400px;
    -moz-border-radius: 400px;
}


#sidebar {
    margin-bottom: -25px;
    min-width: 250px;
    max-width: 250px;
    height: 100vh;
    background-color:rgba(230, 133, 43, .8);
    color: #fff;
    -webkit-transition: all 0.6s cubic-bezier(0.945, 0.020, 0.270, 0.665);
    -o-transition: all 0.6s cubic-bezier(0.945, 0.020, 0.270, 0.665);
    transition: all 0.6s cubic-bezier(0.945, 0.020, 0.270, 0.665);
    -webkit-transform-origin: bottom left;
        -ms-transform-origin: bottom left;
            transform-origin: bottom left;
    margin-top: 5px;
}

#sidebar.active {
    margin-left: -250px;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}

#sidebar .sidebar-header {
    padding: 20px;
    background: #6d7fcc;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid grey;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
    color: white;
}
#sidebar .active,
#sidebar ul li a:hover {
    color: #fff;
}

#sidebar ul li.active > a, a[aria-expanded="true"] {
    color: #fff;
    background: #281942;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
}

ul.CTAs {
    padding: 20px;
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
}

a.download {
    background: #fff;
    color: #7386D5;
}

a.article, a.article:hover {
    background: #6d7fcc !important;
    color: #fff !important;
}
.modalButton{
    margin-top: 10px;
    padding: 0px;
}
.socialbuttons{
    width: 100%;
    vertical-align: top;
    display: inline-block;
    margin-right: 5px;
}
.SocialMediaShareButton{
    cursor: pointer;
}
.mediadiv{
    vertical-align: top;
    display: inline-block;
    margin-right: 10px;
    text-align: center;
}
#points{
    color:rgb(56, 56, 128);
    font-weight: bold;

}
.hoursPoints{
    color: rgb(56, 56, 128);
    font-weight: bold;
}

@media (max-width: 768px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
    #content {
        width: 100%;
    }
    #content.active {
        width: calc(100% - 250px);
    }
    #sidebarCollapse span {
        display: none;
    }
}


